.next-level-btn {
  color: #fff;
  font-size: 25px;
  width: 100%;
  background-color: #008596;
  border: none;
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
  transition: all .1s ease-in-out; }
  .next-level-btn:hover {
    transition: all .1s ease-in-out;
    background-color: #00b2c9; }

.next-level-btn.disabled {
  background-color: rgba(83, 83, 83, 0.61);
  color: rgba(255, 255, 255, 0.479);
  outline: none; }
