body {
  font-size: 16px;
  font-family: 'Raleway', sans-serif;
  background-color: #111014;
  color: #f0fdff;
  padding: 5px; }

.container {
  max-width: 1140px;
  margin: 0 auto; }

.field-card {
  background-color: #1d1b22;
  border-radius: 5px;
  padding: 20px; }
  .field-card__img {
    display: inline-block;
    width: 25%;
    margin-right: 20px; }
    .field-card__img img {
      background-color: white;
      padding: 1px;
      width: 100%;
      border-radius: 10px;
      min-height: 150px;
      max-height: 250px;
      overflow: hidden; }
  .field-card__body {
    margin-top: 10px;
    display: inline-block;
    vertical-align: top;
    width: 65%; }
  .field-card__name {
    font-size: 30px;
    font-weight: bold; }
    .field-card__name::after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      transform: translateX(-5px);
      margin-top: 10px;
      margin-bottom: 10px;
      background-color: rgba(255, 255, 255, 0.384); }
  .field-card__audio {
    display: flex;
    width: 100%;
    border: none;
    background-color: transparent;
    box-shadow: none;
    color: #fff; }
    .field-card__audio #rhap_current-time, .field-card__audio .rhap_time.rhap_total-time {
      color: #fff; }
    .field-card__audio .rhap_container {
      width: 100%; }
      .field-card__audio .rhap_container:focus {
        outline: none !important; }
    .field-card__audio .rhap_volume-controls {
      display: block;
      outline: none; }
    .field-card__audio .rhap_volume-container {
      width: 100%;
      outline: none; }
    .field-card__audio .rhap_button-clear svg path {
      fill: #fff; }
    .field-card__audio .rhap_progress-indicator, .field-card__audio .rhap_volume-indicator {
      background-color: #008c9e; }
    .field-card__audio .rhap_forward-button, .field-card__audio .rhap_rewind-button {
      display: none; }

@media screen and (max-width: 550px) {
  .field-card__img {
    display: block;
    margin: 0 auto;
    width: 100%; }
    .field-card__img img {
      width: 100%; }
  .field-card__name {
    width: 100%; }
  .field-card__body {
    width: 100%; }
  .rhap_container {
    padding: 0; } }
