.field-card.field-choice {
  width: calc(100% - 90px);
  display: inline-block;
  margin-top: 10px;
  height: 290px; }

.field-choice ul {
  padding: 0;
  list-style: none; }

.field-choice__item {
  border-bottom: 1px solid rgba(165, 189, 192, 0.404);
  border-left: 0;
  border-right: 0; }
  .field-choice__item:first-child {
    border-top: 1px solid rgba(165, 189, 192, 0.404); }
  .field-choice__item button {
    padding: 10px;
    outline: none;
    border: none;
    background-color: transparent;
    color: #fff;
    width: 100%;
    text-align-last: left;
    transition: all .1s ease-in-out; }
    .field-choice__item button:hover {
      transition: .1s ease-in-out;
      transform: scale(1.05); }
    .field-choice__item button:active {
      background-color: #292630; }
    .field-choice__item button::before {
      content: '';
      text-align: right;
      display: inline-block;
      margin-right: 10px;
      width: 10px;
      height: 10px;
      padding: 2px;
      background-color: rgba(165, 189, 192, 0.404);
      border-radius: 50%; }
  .field-choice__item.failure button {
    color: rgba(255, 255, 255, 0.226); }
    .field-choice__item.failure button::before {
      content: '\2718';
      font-size: 10px;
      color: #fff;
      background-color: #FF2E63; }
  .field-choice__item.pass button {
    font-weight: bold; }
    .field-choice__item.pass button::before {
      content: '\2714';
      font-size: 10px;
      color: #fff;
      background-color: #9dd3a8; }

@media screen and (max-width: 700px) {
  .field-card.field-choice {
    width: auto;
    display: block; } }
