.game-over {
  margin-top: 50px;
  background-color: #1d1b22;
  padding: 20px;
  border-radius: 9px;
  min-height: 500px; }
  .game-over__header {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    width: 70%;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 1px solid #fff;
    margin-bottom: 20px; }
  .game-over__body {
    font-size: 20px;
    text-align: center;
    border-right: 1px solid #fff;
    border-left: 1px solid #fff;
    padding: 20px 0; }
  .game-over__score span {
    font-weight: bold;
    font-size: 25px;
    margin-left: 5px; }
  .game-over__restart-btn {
    margin-top: 20px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    padding: 16px 32px;
    background-color: #008c9e;
    border-radius: 5px;
    border: none;
    transition: all .2s ease-in-out; }
    .game-over__restart-btn:hover {
      transition: all .2s ease-in-out;
      background-color: #00dffc; }

.congrats {
  margin-top: 20px; }
  .congrats img {
    margin-top: 50px; }
