.level-map {
  display: flex;
  justify-content: center;
  padding: 0;
  list-style: none;
  padding-top: 0px; }
  .level-map__item {
    color: rgba(255, 255, 255, 0.493);
    text-align: center;
    width: calc(100% / 5);
    background-color: #005f6b;
    padding: 5px;
    position: relative; }
    .level-map__item:last-child {
      border-radius: 0 8px 8px 0; }
      .level-map__item:last-child::after {
        content: ''; }
    .level-map__item:first-child {
      border-radius: 8px 0 0 8px; }
    .level-map__item.active {
      background-color: #008c9e;
      color: #fff; }
      .level-map__item.active::after {
        content: '';
        display: inline-block;
        position: absolute;
        right: -35px;
        top: 50%;
        z-index: 200;
        vertical-align: 3px;
        border-top: 25px solid #008c9e;
        border-right: 24px solid transparent;
        border-left: 24px solid transparent;
        transform: translateY(-50%) rotate(-90deg); }

@media screen and (max-width: 700px) {
  .level-map {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }
    .level-map__item {
      padding: 5px;
      width: 30%; }
      .level-map__item:last-child {
        border-radius: 0; }
      .level-map__item:first-child {
        border-radius: 0; }
      .level-map__item.active::after {
        content: '';
        border: none; } }
